<template>
  <div
    v-if="hotelInfo"
    id="scroll"
  >
    <div id="hotel-info">
      <span>门店信息</span>
      <span class="info-green">{{ hotelStatus[hotelInfo.status] }}</span>
      <van-icon
        name="qr"
        color='#02bc6c'
        @click="getCode"
      />
    </div>
    <div
      id='hotel-top'
      class="info-hotel"
      @click="goEditNext('/addOrEditHotel')"
    >
      <div class="hotel-left">
        <img
          v-if="hotelInfo.logo"
          :src="hotelInfo.logo"
          @click.stop="goNext('/uploadHotelPic',{type:'hotel',hotelId:hotelInfo.id,logoPic:hotelInfo.logo})"
          class='left-img'
          style="width:80px;height:70px;"
        >
        <img
          v-else
          src="@/assets/hotelAudit/normal.png"
        >
        <span class="common-title img-title">图片管理</span>
        <div class="left-title">
          <div class="c_000">{{ hotelInfo.hotelName }}</div>
          <div class="adr">{{ hotelInfo.province }}{{ hotelInfo.city }}{{ hotelInfo.county }}{{ hotelInfo.location }}</div>
          <div style="font-size:12px;color:#393939;">{{ hotelInfo.phone }}</div>
        </div>
      </div>
      <div class="hotel-edit">
        <div class="c_39">修改</div>
        <van-icon
          class="right-img"
          name="arrow"
        />
      </div>
    </div>
    <div
      id="hotel-info"
      style="margin-top:0;"
    >房型信息</div>
    <div id="hotel-center">
      <div
        v-for="(item, i) in HotelStateList"
        :key="i"
        @click="goRoomState('/scaleManage',item)"
      >
        <div class="info-hotel">
          <div class="hotel-left">
            <div
              style="-border:1px solid red;width:60px;"
              @click.stop="goNext('/uploadHotelPic',{type:'scale',roomScaleId:item.id,logoPic:item.logo})"
            >
              <img
                v-if="item.logo"
                :src="item.logo"
                class='left-img'
              >
              <img
                v-else
                src="@/assets/hotelAudit/normal.png"
                class='left-img'
              >
              <span class="common-title scale-title">图片管理</span>
            </div>
            <div class="left-title">
              <div class="c_000">{{ item.scaleName }}</div>
              <div class="adr">{{ item.roomSize }}m²&ensp;{{ item.bedNum == '1'?'大床':"双床" }}</div>
              <div style="font-size:13px;color:#000;">共{{ item.roomCount }}间
              </div>
            </div>
          </div>
          <div class="hotel-right">
            <div class="right-top">
              <div class="c_39">编辑</div>
              <van-icon
                class="right-img"
                name="arrow"
              />
            </div>
          </div>
        </div>
      </div>
      <mt-popup
        v-model="popupVisible"
        position="center"
      >
        <img
          v-if="pic"
          style="display:inline-block;width:300px;height:300px;"
          :src="`http://qiniu.aiyoyoufang.com/${pic}`"
          alt
        />
        <p>长按图片保存</p>
      </mt-popup>
      <van-button
        class="add-scale"
        @click="goRoomState('/scaleManage')"
      >+&ensp;添加房型</van-button>
    </div>
  </div>
</template>
<script>
import { GetBusinessHotel, PostBusinesSroomscaleRemove } from '@/api/api_hotel.js';
import { GetBusinessRoomscaleByhotel, GetBusinessHotelUnionCode } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Dialog } from 'vant';
export default {
    data() {
        return {
            hotelInfo: {},
            HotelStateList: [],
            hotelStatus: {},
            popupVisible: false,
            pic: null
        };
    },
    created() {
        this.$store.commit('setHeader', '房型管理');
        this.getHotelInfo();
    },
    methods: {
        // 酒店信息
        getHotelInfo() {
            GetBusinessHotel({ hotelId: this.$route.query.hotelId })
                .then((res) => {
                    this.hotelInfo = res.data;
                    this.getType();
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 查询字典
        getType() {
            GetSystemDictDataByType({ dictType: 'hotel_status' }).done((res) => {
                this.HotelStateListGet();
                this.hotelStatus = {};
                res.data.map((item) => {
                    this.hotelStatus[item.dictValue] = item.dictLabel;
                });
            });
        },
        // 获取基础房型信息
        HotelStateListGet() {
            GetBusinessRoomscaleByhotel(this.$route.query.hotelId).done((res) => {
                this.HotelStateList = res.data;
            });
        },
        // 查看二维码
        getCode() {
            GetBusinessHotelUnionCode(this.$route.query.hotelId).done((res) => {
                this.popupVisible = true;
                this.pic = res.data;
            });
        },
        // 删除房型
        removeScale(item) {
            Dialog.confirm({
                title: '确定删除？',
                message: `基础房型：${item.scaleName}`
            })
                .then(() => {
                    PostBusinesSroomscaleRemove({ roomScaleId: item.id }).done((res) => {
                        this.HotelStateListGet();
                    });
                })
                .catch(() => {});
        },
        // 编辑房型（基础房型，产品房型，房间）
        goRoomState(url, info) {
            if (!info) {
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId,
                        endLineTime: this.hotelInfo.endLineTime,
                        charge: this.hotelInfo.charge,
                        round: '1'
                    }
                });
                localStorage.removeItem('addoredit_scaleName');
            } else {
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId,
                        id: info.id,
                        scaleName: info.scaleName,
                        endLineTime: this.hotelInfo.endLineTime,
                        charge: this.hotelInfo.charge,
                        round: '1'
                    }
                });
                let obj = {
                    scaleName: info.scaleName,
                    roomScaleId: info.id
                };
                localStorage.setItem('addoredit_scaleName', JSON.stringify(obj));
            }
            localStorage.removeItem('myscale_Actived');
        },
        // 编辑酒店
        goEditNext(url) {
            this.$router.push({
                path: url,
                query: {
                    hotelId: this.$route.query.hotelId
                }
            });
        },
        // 编辑酒店图片和房型图片
        goNext(url, obj) {
            if (obj.type == 'hotel') {
                if (obj.logoPic) {
                    localStorage.setItem('topPic', obj.logoPic);
                } else {
                    localStorage.removeItem('topPic');
                }
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId,
                        type: obj.type
                    }
                });
            } else {
                if (obj.logoPic) {
                    localStorage.setItem('topPic', obj.logoPic);
                } else {
                    localStorage.removeItem('topPic');
                }
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId,
                        type: obj.type,
                        roomScaleId: obj.roomScaleId
                    }
                });
            }
        }
    }
};
</script>
<style scoped>
#scroll {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
#hotel-info {
    padding: 10px;
    text-align: left;
    font-size: 14px;
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    margin-top: 10px;
    display: flex;
    align-items: center;
}
.info-green {
    color: #02bc6c;
    font-size: 10px;
    margin: 0 10px;
    border: 1px solid #02bc6c;
    font-weight: normal;
    padding: 0px 2px;
}
#hotel-top {
    margin: 0 0 10px;
    background: #fff;
}
#hotel-center {
    font-size: 14px;
    background: #fff;
    margin-bottom: 20px;
}
.info-hotel {
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #efeff4;
    color: #b2b2b2;
}
.hotel-left {
    display: flex;
    align-items: center;
    text-align: left;
    width: 76%;
    position: relative;
}
.left-img {
    width: 60px;
    height: 66px;
    border-radius: 4px;
}
.common-title {
    font-size: 12px;
    text-align: center;
    background: #000;
    opacity: 0.6;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    color: #fff;
}
.img-title {
    position: absolute;
    left: 0px;
    top: 54px;
    width: 80px;
}
.scale-title {
    position: absolute;
    top: 50px;
    left: 0px;
    width: 60px;
}
.left-title {
    width: 60%;
    height: 60px;
    margin-left: 10px;
}
.hotel-right {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.hotel-edit {
    display: flex;
    align-items: center;
}
.right-top {
    display: flex;
    align-items: center;
}
.add-scale {
    color: #1989fa;
    line-height: 40px;
    width: 100%;
}
.c_000 {
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.c_39 {
    color: #393939;
    font-size: 13px;
}
.adr {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    height: 30%;
    margin: 2px 0;
}
</style>



